.bodylogin {
  width: 25rem;
  margin: auto;
  padding-top: 20vh;
}
.login {
  width: 100%;
  height: 60vh;
  position: relative;
  border-radius: 0.5rem;
  box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
  padding: 0;
  background-color: rgb(255, 255, 255);
}
.login button {
  background-color: #603813;
  color: rgb(255, 255, 255);
  border-radius: 0.5rem;
  width: 30vh;
}
.login input {
  width: 100%;
}
.title-login {
  color: rgb(255, 255, 255);
  height: 15vh;
  font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
  font-weight: 700;
  text-align: center;
  width: 90%;
  font-size: 2rem;
  position: absolute;
  left: 5%;
  border-radius: 0.5rem;
  padding-top: 0.8rem;
  top: -5vh;
  background: #603813; /* fallback for old browsers */
  background: -webkit-linear-gradient(
    to right,
    #b29f94,
    #603813
  ); /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(
    to right,
    #b29f94,
    #603813
  ); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}
.btn-detial {
  background-color: #664729;
  color: rgb(255, 255, 255);
  border-radius: 0.5rem;
  padding: 0.5rem 0.8rem;
  border: none;
}
.titleadmin {
  margin: auto;
  text-align: center;
  font-size: 1.5rem;
  font-weight: 700;
  width: 100%;
  padding: 1rem 2rem;
  position: relative;
}
.title-login div {
  margin-top: 1rem;
}
.titleadmin button {
  position: absolute;
  right: 1rem;
  top: 0.4rem;
}
.input-login {
  width: 80vh;
}
.bodyadmin {
  margin: auto;
  width: 100%;
  height: 100vh;
  padding: 2rem;
  border-radius: 0.5rem;
}
.datatables {
  background-color: rgb(255, 255, 255);
  box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
  border-radius: 0.5rem;
  padding: 1rem 2rem;
  margin: auto;
  width: 80%;
}
.chart-data {
  /* max-width: 50vh; */
  /* height: 50vh; */
  width: 90%;
  height: 40pc;
  margin-left: 5%;
  padding: 3rem 5rem;
  background-color: white;
  border-radius: 0.5rem;
  display: inline-block;
}
.charts{
  width: 95%;
  border-radius: 0.5rem;
  padding: 2rem 1rem 2rem 2rem;
  margin: auto;
  box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
  background-color: rgb(255, 255, 255);
  position: relative;
  margin-bottom: 1rem;
}
.table-data {
  width: 100%;
}
.charts-c1{
  width: 3.9rem;
  height: 1.2rem;
  position: absolute;
  background-color: rgb(255, 255, 255);
  z-index: 10;
  left: 1;
  bottom: 7rem;
}
/* .charts-c2{
  width: 5rem;
  height: 3rem;
  position: absolute;
  background-color: rgb(255, 255, 255);
  z-index: 10;
  right: 1rem;
  bottom: 0;
} */
.data-table{
  border-radius: 0.5rem;
  margin: auto;
  width: 95%;
  background-color: white;
  padding: 2rem;
  overflow-x: scroll;
}
.title-user{
  width: 95%;
  text-align: center;
  padding: 2rem;
  color: white;
  font-size: 1.5rem;
  font-family:monospace;
  position: relative;
  font-weight: 500;
  border-radius: 1rem;
  margin: 1rem auto;
  background: #BA8B02;  /* fallback for old browsers */
background: -webkit-linear-gradient(to right, #181818, #BA8B02);  /* Chrome 10-25, Safari 5.1-6 */
background: linear-gradient(to right, #181818, #BA8B02); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */

}
.fromlogin{
  width: 80%;
  margin: auto;
  margin-top: 8rem;
}
/* .fromlogin button{
  width: 60%;
  background-color: #664729;
} */
.login-user{
  position: absolute;
  font-size: 1rem;
  padding: 0.5rem 1.5rem;
  right: 1rem;
  top: 1.8rem;
}
@media screen and (max-width: 992px) {
  .datatables {
    width: 100%;
    padding: 0.2rem;
  }
  .table-data {
    overflow-x: auto;
  }
  .title-userss{
    margin: -1rem -2rem;
    width: 85%;
  }
}
@media screen and (max-width: 600px) {
  .titleadmin {
    text-align: start;
  }
  .titleadmin button {
    right: 0.2rem;
  }
  .table-data {
    overflow-x: scroll;
  }
  .login {
    padding: 2rem;
    height: 70vh;
  }
  .bodyadmin {
    padding: 0.5rem;
  }
  .bodylogin {
    padding-top: 12vh;
  }
}
